export default {
    name: 'click-tag',
    type: 'action',
    btnText: '',
    info: '',
    props: {
        value: {
            type: 'text',
            info: '',
            default: '1'
        }
    },
    color: 'red darken-2',
    display({ value }) {
        return ` ${value} `;
    },
    write({ value }) {
        return `
            let handles = await browser.getAllWindowHandles()
            await browser.switchTo().window(handles[${Number(value) - 1}])
        `;
    }
};
