import { defineTemplate } from '../define';
export default defineTemplate({
    props: {
        value: {
            type: 'text',
            info: '',
            default: '1000'
        }
    },
    template: {
        name: 'wait',
        type: 'action',
        btnText: '',
        info: '',
        help: 'N',
        color: 'purple accent-1',
        display({ value }) {
            return ` ${value} `;
        },
        validate({ value }) {
            let result = Number(value);
            if (isNaN(result)) {
                return '';
            }
            else {
                return true;
            }
        },
        write(data) {
            return `await browser.sleep(${data.value})`;
        }
    }
});
