export default {
    name: 'verify-tag',
    type: 'verify',
    btnText: '',
    info: '',
    props: {
        value: {
            type: 'text',
            info: '',
            default: '1'
        }
    },
    color: 'red darken-1',
    display({ value }) {
        return ` ${value}`;
    },
    write({ value }) {
        return `
            let handles = await browser.getAllWindowHandles()
            await expect(handles.length).toEqual(${value})
        `;
    }
};
