export default {
    name: 'set-path',
    type: 'system',
    btnText: '',
    info: '',
    props: {
        value: {
            type: 'text',
            info: '',
            default: ''
        }
    },
    color: 'blue-grey darken-1',
    display({ value }) {
        return ` ${value}`;
    },
    write({ value }) {
        return `await browser.driver.get('${value}')`;
    }
};
