export default {
    name: 'count',
    type: 'verify',
    btnText: '',
    info: 'query',
    props: {
        selector: {
            type: 'text',
            info: 'query selector',
            default: '.'
        },
        count: {
            type: 'text',
            info: 'query',
            default: '1'
        }
    },
    color: 'lime darken-2',
    display({ selector, count }) {
        return ` ${selector}  ${count} `;
    },
    write({ selector, count }) {
        return `await expect($$('${selector}').count()).toBe(${count})`;
    }
};
