export default {
    name: 'click-btn',
    type: 'action',
    btnText: '',
    info: '',
    props: {
        value: {
            type: 'text',
            info: '',
            default: ''
        }
    },
    color: 'teal lighten-2',
    display({ value }) {
        return ` ${value} `;
    },
    write({ value }) {
        return `await element(by.buttonText('${value}')).click()`;
    }
};
